@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
/* color palette */
:root {
  /*  */
  --pink: rgba(252, 169, 169, 0.6);
  --pink1: #fe8daa;
  --pink2: #dfdbdf;
  --pink3: #fe8daa;
  /*  */
  --one: rgb(253, 123, 57);
  --two: rgb(238, 114, 60);
  --three: rgb(249, 150, 51);
  --four: rgb(48, 34, 33);
  /*  */
  --lightgray: rgb(250, 252, 254);
  --slightgray: rgb(173, 173, 173);
}

.music-player-item {
  height: 70px;
  /* width: 200px; */
  margin: 10px 0;
  padding: 10px;
  border-top: solid 1.5px rgb(173, 173, 173);
  border-top: solid 1.5px var(--slightgray);
  border-bottom: solid 1.5px rgb(173, 173, 173);
  border-bottom: solid 1.5px var(--slightgray);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

/* ==================================== */

.song-number {
  color: rgb(173, 173, 173);
  color: var(--slightgray);
  margin-right: 10px;
  font-size: 15px;
  width: 25px;
}

/* =============================--- */

.play-pause-container {
  margin: 0 20px 0 0;
}

.play-pause {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(to bottom right, rgb(238, 114, 60), rgb(249, 150, 51));
  background: linear-gradient(to bottom right, var(--two), var(--three));
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: all 0.2s ease-in-out;
  -webkit-transform-origin: center;
          transform-origin: center;
  cursor: pointer;
}

.play-pause:hover {
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.play-pause-button {
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
  color: white;
  font-size: 80%;
  cursor: pointer;
}

.progress-container {
  background-color: #dfdbdf;
  border-radius: 6px;
  cursor: pointer;
  height: 3px;
  width: 80px;
}

.progress {
  background-color: rgb(249, 150, 51);
  background-color: var(--three);
  border-radius: 6px;
  height: 100%;
  width: 80%;
  transition: width 0.1s linear;
}

/* ========================================- */

@-webkit-keyframes slidein {
  from {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes slidein {
  from {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
  }
}

.song-details-container {
  height: 50px;
  min-width: 100px;
  border-radius: 5px;
  z-index: 10;
}

.details-i-container {
  height: 50px;
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  position: relative;
}

.on-off-details {
  position: absolute;
  width: 0;
  height: 0;
}

.song-details-container i {
  color: rgb(249, 150, 51);
  color: var(--three);
}

.song-details {
  display: none;
  background: linear-gradient(to bottom right, rgb(238, 114, 60), rgb(249, 150, 51));
  background: linear-gradient(to bottom right, var(--two), var(--three));
  border-radius: 5px;
  padding: 10px;
  -webkit-animation-name: slidein;
          animation-name: slidein;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: cubic-bezier(0.68, -0.55, 0.27, 1.55);
          animation-timing-function: cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.song-details p {
  color: rgb(250, 252, 254);
  color: var(--lightgray);

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* ================================================== */

.toggle-player-menu {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  color: rgb(249, 150, 51);
  color: var(--three);
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.player-menu-holder {
  display: -webkit-flex;
  display: flex;
  height: 50px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.player-menu-item {
  color: rgb(249, 150, 51);
  color: var(--three);
  margin-right: 1em;
  cursor: pointer;
}

/* fonts */
/* color palette */
:root {
  /* material-ui */
  --primary: #1976d2;
  --secondary: #dc004e;
  /*  */
  --one: rgb(253, 123, 57);
  --two: rgb(238, 114, 60);
  --three: rgb(249, 150, 51);
  --four: rgb(48, 34, 33);
  /*  */
  --lightgray: rgb(250, 252, 254);
}

/* ========================================================= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(250, 252, 254);
  background-color: var(--lightgray);
  /* background-color: var(--four); */

  /* overflow-x: hidden; */
}

button {
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

button:focus {
  outline: none;
}

.cursor {
  width: 2rem;
  height: 2rem;
  border: solid 2px rgb(48, 34, 33);
  border: solid 2px var(--four);
  border-radius: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  pointer-events: none;
  display: none;
}

/* =============================================== */

.main {
  width: 100%;
}

/* =============================================== */

.top-menu {
  width: 100%;
  background-image: url(/music-generator/static/media/music-studio.2176135c.jpg);
