  -ms-user-select: none;
  user-select: none;
}

.top-menu-items {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  height: 100%;
  padding: 0 50px 0 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
